.boxes{
    display: inline-flex;
    padding: 10px;
    margin-top: 12vh;
    margin-left: 30vh;
}
.box{
    margin: 10px;
    margin-right: 15vh;
    position: relative;
    width: 125px;
    height: 155px;

    
    text-align:center;
    font-family: Fruktur;
    font-size: 16px;
    
    border-radius: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

    background: rgba(3, 85, 0, 0.99);
}
a{
    text-decoration: none;
    color: black;
}
.box:hover{
    width: 130px;
    height: 160px;
    filter:brightness(0.9)
}
.mexico{
    text-align: center;
    margin-top: 10vh;
}
.mexicoPalavra{
    font-family: roboto;
    font-size: 3vh;
}
.bandeira{
    height:25vh;
}
.icon{
    height: 100px;
    width: 100px;
    margin-bottom: 20px;
    background: rgba(3, 85, 0, 0.99);
}
.Cabeca{
    font-size: 70px;
    text-align: center;
    font-family: sans-serif;
    
}
.topico{
    margin:15px;
}
.titulo{
    display:block;
    font-family: roboto;
    font-size:30px;
    border-bottom: 1px solid #000000;
}
.texto{
    font-size: 22px;
    font-family: arial;
    margin-left: 20px;
    margin-top: 20px;
    margin-bottom:50px;
    text-align: left;
}
img{
    width: 50vh;
}